<template>
	<view class="main">
		<wwqy-header></wwqy-header>
		<view class="content">
			<v-tabs v-model="current" :tabs="tabs" @change="changeTab"></v-tabs>
			<view v-if="tabIndex === 0">
				<view v-for="(weaponItem, index) in weaponList" :key="index">
					<weapon :item="weaponItem"></weapon>
				</view>
			</view>
			<view v-if="tabIndex === 1">1</view>
			<view v-if="tabIndex === 2">2</view>
			<view v-if="tabIndex === 3">3</view>
		</view>
		<wwqyFooter></wwqyFooter>
	</view> 
</template>

<script>
import wwqyHeader from '@/components/wwqy-header/index.vue';
import wwqyFooter from '@/components/wwqy-footer/index.vue';
import weapon from './components/weapon/index.vue';
import hero from './components/hero/index.vue';
import map from './components/map/index.vue';
import tool from './components/weapon/index.vue';

export default {
	components: { wwqyHeader, wwqyFooter, weapon, hero, map, tool },
	data() {
		return {
			current: 0,
			tabIndex: 0, // 当前选中的tab
			tabs: ['武器', '英雄', '地图', '工具'],
			newsInfoData: [{}, {}],
			// 加载信息
			loadingStatus: 'more',
			weaponList: [
				{ name: 'Weapon 1', type: 'Type 1' },
				{ name: 'Weapon 2', type: 'Type 2' },
				{ name: 'Weapon 3', type: 'Type 3' }
			]
		};
	},

	onload() {},
	onShow() {},
	methods: {
		changeTab(index) {
			this.tabIndex = index;
			// console.log('当前选中索引：' + index);
		},
		// 滚动到底部
		onToButtom() {
			if (this.loadingStatus === 'no-more') {
				return;
			}
			this.page++;
			this.loadingStatus = 'loading';
			this.allNewsFn();
		},
		// 点击头部标签
		onTabTap(e) {
			if (e !== undefined) {
				let index = e.target.dataset.current || e.currentTarget.dataset.current;
				// 当前下标改为点击的下标
				this.tabIndex = index;
			}
			this.page = 1;
			// 当前下标改为点击的下标
			// 当前tabbar选中Id改为点击的Id
			if (this.tabBars && this.tabBars[this.tabIndex]) {
				this.scrollInto = this.tabBars[this.tabIndex].id;
			}
		},
		// tab拖动切换事件
		onTabChange(e) {
			let index = e.detail.current;
			this.page = 1;
			// 当前下标改为点击的下标
			this.tabIndex = index;
			// 当前tabbar选中Id改为点击的Id
			this.scrollInto = this.tabBars[index].id;
		}
	}
};
</script>

